.dt-container {
  // Toolbar
  > :first-child {
    @extend #{'.px-3', '.pt-2', '.gap-2'};
  }

  // Table it self
  table.dataTable {
    > tbody > tr.child {
      padding: 0;

      > td.child {
        padding: 0;

        ul.dtr-details {
          --cui-bg-opacity: 0.75;
          @extend .bg-body-tertiary;
          width: 100%;

          > li {
            padding: 0.5em;
            border-bottom-color: $table-border-color;

            > .dtr-title {
              padding-inline-end: 0.5em;
            }
          }
        }
      }
    }

    &.dtr-inline.collapsed > tbody > tr {
      $arrow-tip-style: 12px solid rgba(110, 110, 110, 0.5);
      $arrow-side-style: 6px solid transparent;
      $arrow-bottom-style: 0 solid transparent;

      > td.dtr-control::before {
        border-top: $arrow-side-style;
        border-right: $arrow-bottom-style;
        border-bottom: $arrow-side-style;
        border-left: $arrow-tip-style;
      }
      &.dtr-expanded > td.dtr-control::before {
        border-top: $arrow-tip-style;
        border-right: $arrow-side-style;
        border-bottom: $arrow-bottom-style;
        border-left: $arrow-side-style;
      }
    }
  }

  // Pagination
  > :last-child {
    @extend #{'.px-3', '.pb-3', '.pt-2', '.gap-2'};

    align-items: center;

    .dt-info {
      @extend .pt-0;
    }
  }
}
